<template>
  <div class="app">
    <Header></Header>
    <span class="reg">{{ name }}</span>
    <button class="reg" @click="login" v-if="showSingIn">Войти</button>
    <form class="reg" id="search">
    Поиск:ㅤ
    <input name="query" v-model="searchQuery">
    </form>
    <Grid :data="gridData" :columns="gridColumns" :filter-key="searchQuery"></Grid>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Grid from "@/components/Grid.vue";
import Keycloak from "keycloak-js";

const keycloak = new Keycloak({
  url: "https://keys.urfu.ru/auth/",
  realm: "urfu-lk",
  clientId: "rtf-eduload",
  public_key:
    "",
  "token-service":
    "https://keys.urfu.ru/auth/realms/urfu-lk/protocol/openid-connect",
  "account-service": "https://keys.urfu.ru/auth/realms/urfu-lk/account",
  "tokens-not-before": 0,
});

export default {
  mounted() {
    this.fetchJson();
    this.updateKC();
  },
  components: {
    Header,
    Grid,
  },
  data() {
    return {
      showSingIn: true,
      name: "Not Auth",
      isAuth: false,
      searchQuery: '',
      gridColumns: [
        "ФИО",
        "Должность/Место работы",
        "Ставка",
        "Часы на ставку",
        "Кол-во часов на часовую оплату",
        "Планируемое кол-во часов (БЮДЖЕТ)",
        "Фактическое кол-во часов (БЮДЖЕТ)",
        "Разница (БЮДЖЕТ)",
        "Планируемое кол-во часов (КОНТРАКТ)",
        "Фактическое кол-во часов (КОНТРАКТ)",
        "Разница (КОНТРАКТ)",
        "Планируемое кол-во часов (ОБЩЕЕ)",
        "Фактическое кол-во часов (ОБЩЕЕ)",
        "Разница (ОБЩЕЕ)",
        "Год",
      ],
      gridData: [],
    };
  },
  methods: {
    async updateKC()
    {
      await keycloak.init({
      onLoad: "check-sso",
    });
    if (keycloak.authenticated) {
      this.name = keycloak.tokenParsed.user.person.title;
      this.showSingIn = false;
      }
    },
    async login() {
      if (!keycloak.authenticated) {
        await keycloak.login();
      } else {
        this.name = keycloak.tokenParsed.user.person.title;
        this.isAuth = true;
      }
    },
    async fetchJson() {
      let data = [];
      let json;
      await fetch("http://212.193.88.196:8080/loaddata")
        .then((resp) => resp.text())
        .then((razm) => (json = razm));
      
      data = JSON.parse(json);
      console.log(data);

      data.forEach((row) => {
        let element = {
          ФИО: row.name,
          "Место работы/Должность": row.infoWorkPlaces,
          Ставка: row.stake,
          "Часы на ставку": row.hoursOnStake,
          "Кол-во часов на часовую оплату": row.hours,
          "Планируемое кол-во часов (БЮДЖЕТ)": row.bHoursPlaned,
          "Фактическое кол-во часов (БЮДЖЕТ)": row.bHoursReal,
          "Разница (БЮДЖЕТ)": row.bHoursDiff,
          "Планируемое кол-во часов (КОНТРАКТ)": row.cHoursPlaned,
          "Фактическое кол-во часов (КОНТРАКТ)": row.cHoursReal,
          "Разница (КОНТРАКТ)": row.cHoursDiff,
          "Планируемое кол-во часов (ОБЩЕЕ)": row.hoursPlaned,
          "Фактическое кол-во часов (ОБЩЕЕ)": row.hoursReal,
          "Разница (ОБЩЕЕ)": row.hoursDiff,
          Год: row.year,
        };
        this.gridData.push(element);
      });
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.reg {
  display: flex;
  margin-top: 15px;
  margin-left: 15px;
  font-size: 20px;
}
</style>
